<template>
  <div class="col-4 mb-4">
    <div class="card">
      <img class="card-img-top" src="http://via.placeholder.com/350px115/" alt="Card image cap">
      <div class="card-body">
        <h3 class="card-title">{{product.user_id}}</h3>
        <h5 class="card-title">{{product.name}}</h5>
        <p class="card-text">{{product.price}}</p>
      </div>
      <div class="card-footer">
        <a href="#" class="btn btn-primary">Button</a>
      </div>
    </div>
  </div>
</template>

<script>
export default{
  props: ['product']
}
</script>



