<template>
  <nav class="navbar navbar-expand-lg navbar-light bg-light mb-3">
    <a class="navbar-brand" href="#">Navbar</a>
      <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
        <span class="navbar-toggler-icon"></span>
      </button>

    <div class="collapse navbar-collapse" id="navbarSupportedContent">
      <ul class="navbar-nav mr-auto">
        <!-- link biasa
        <li class="nav-item">
          <a href="#" class="nav-link">Login</a>
        </li>
        <li class="nav-item">
          <a href="#" class="nav-link">Register</a>
        </li>
        -->
        <li class="nav-item">
          <router-link class="nav-link" to="/login" v-if=" ! isAuth">
            Login
          </router-link>
        </li>
        <li class="nav-item">
          <router-link class="nav-link" to="/register" v-if=" ! isAuth">
            Register
          </router-link>
        </li>
        <li class="nav-item">
          <router-link class="nav-link" to="/feed" v-if="isAuth">
            Feed
          </router-link>
        </li>
        <li class="nav-item">
          <router-link class="nav-link" to="/products/create" v-if="isAuth">
            Create
          </router-link>
        </li>
        <li class="nav-item">
          <router-link class="nav-link" to="/products/edit" v-if="isAuth">
            Edit
          </router-link>
        </li>
        <li class="nav-item">
          <router-link class="nav-link" to="/logout" v-if="isAuth">
            Logout
          </router-link>
        </li>
      </ul>

      <form class="form-inline my-2 my-lg-0">
        <input class="form-control mr-sm-2" type="search" placeholder="Search" aria-label="Search">
        <button class="btn btn-outline-success my-2 my-sm-0" type="submit">Search</button>
      </form>
    </div>
  </nav>
</template>

<script>
export default {
  data () {
    return {
      isAuth: null
    }
  },
  created () {
    this.isAuth = this.$auth.isAuthenticated()
    // console.log(this.isAuth)
    // this.setAuthenticatedUser()
  },
  methods: {
    setAuthenticatedUser () {
      this.$http.get('api/user')
        .then(response => {
          this.$auth.setAuthenticatedUser(response.body)
          // console.log(this.$auth.getAuthenticatedUser())
        })
    }
  }
}
</script>



